.card{
  border: 1px solid #F3F3F3;
  border-radius: 40px;
  padding: 30px;
  width: 150px;
  max-height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: box-shadow 0.3s ease-in-out,  transform 0.3s ease-in-out;

  button {
    width: 32px;
    height: 32px;
    border: 1px solid #F2F2F2;
    border-radius: 8px;
    box-sizing: border-box;
    cursor: pointer;
    padding: 7px;
  }



  &:hover{
    box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.07);
    border: 1px solid #F8F8F8;
    transform: translateY(-5px);
  }


  p{
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    padding-top: 14px;
    padding-bottom: 14px;

  }

  .favorite{
    position: absolute;
    cursor: pointer;


  }


  .price {
    display: flex;
    justify-content: space-between;
    align-items: center;

      div{
        display: flex;
        flex-direction: column;
        gap: 2px;
      }

      span{
        font-weight: 500;
        font-size: 11px;
        line-height: 13px;
        text-transform: uppercase;
        color: #BDBDBD;

      }

      b{
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
      }
  }
}

